import { uploadApi } from "api/upload";
import React from "react";
import { Col, Row } from "react-bootstrap";
import { useFieldArray, useForm, useFormContext } from "react-hook-form";
import { FormattedMessage, useIntl } from "react-intl";
import { toast } from "react-toastify";
import { v4 } from "uuid";
import styles from "./index.module.scss";

const EducationForm = () => {
  const { control } = useFormContext();
  const { formatMessage } = useIntl();
  const { fields, append, remove } = useFieldArray({
    name: "education",
    control: control,
  });
  const { register, handleSubmit, errors } = useForm();

  //   handlers Mehtods
  const addEducationHandler = async ({ certification, ...data }) => {
    const file = new FormData();
    file.append("files", certification[0]);
    try {
      const fileResponse = await uploadApi(file);
      append({ ...data, certification: fileResponse?.[0] });
    } catch (error) {
      toast.error(error);
    }
  };
  return (
    <form onSubmit={handleSubmit(addEducationHandler)}>
      <Row className={styles.innrRow}>
        <Col xs={12}>
          <select
            className={`${errors?.["level"] ? styles.errorField : ""}
          `}
            {...register("level", { required: true })}
          >
            <option value={""}>
              {formatMessage({ id: "levelPlaceHolder" })}
            </option>
            <option value={"postgraduate"}>
              {formatMessage({ id: "postgraduate" })}
            </option>
            <option value={"high"}>{formatMessage({ id: "high" })}</option>
            <option value={"average"}>
              {formatMessage({ id: "average" })}
            </option>
            <option value={"mediocre"}>
              {formatMessage({ id: "mediocre" })}
            </option>
            <option value={"withoutEducation"}>
              {formatMessage({ id: "withoutEducation" })}
            </option>
          </select>
        </Col>
        <Col xs={12}>
          <input
            type="text"
            className={`${errors?.["university"] ? styles.errorField : ""}
            `}
            placeholder={formatMessage({ id: "university" })}
            {...register("university", { required: true })}
          />
        </Col>

        <Col xs={12} lg={6}>
          <input
            type="date"
            className={`${errors?.["startDate"] ? styles.errorField : ""}
            `}
            placeholder="تاريخ بداية الدراسة"
            {...register("startDate", { required: true })}
          />
        </Col>
        <Col xs={12} lg={6}>
          <input
            type="date"
            className={`${errors?.["endDate"] ? styles.errorField : ""}
            `}
            placeholder="تاريخ انتهاء الدراسة"
            {...register("endDate", { required: true })}
          />
        </Col>
        <Col xs={12}>
          <label htmlFor="certification" className={styles.fileInput}>
            <p>
              <FormattedMessage id="certification" />
            </p>
            <input
              type="file"
              className={`${styles.fileInput} ${
                errors?.["certification"] ? styles.errorField : ""
              }
              `}
              id="certification"
              {...register("certification", { required: false, type: "mix" })}
            />
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M14 0a5 5 0 0 1 5 5v12a7 7 0 1 1-14 0V9h2v8a5 5 0 0 0 10 0V5a3 3 0 1 0-6 0v12a1 1 0 1 0 2 0V6h2v11a3 3 0 1 1-6 0V5a5 5 0 0 1 5-5Z"
                fill="currentColor"
              />
            </svg>
          </label>
        </Col>
        <Col xs={12}>
          <textarea
            name=""
            id=""
            className={`${errors?.["notesPlaceholder"] ? styles.errorField : ""}
            `}
            cols="30"
            rows="5"
            placeholder={formatMessage({ id: "notesPlaceholder" })}
            {...register("notes", { required: true })}
          />
        </Col>
        <Col xs={12}>
          <input
            type="submit"
            value=" إضافة تعليم +"
            className={styles.addField}
          />
        </Col>
        <Col xs={12}>
          {fields?.map((tableItem, index) => (
            <div className={styles["table"]}>
              <div className={styles["table-body"]}>
                <table>
                  {Object.entries(tableItem).map(
                    ([key, value]) =>
                      key !== "id" && (
                        <tr key={v4()}>
                          <th>
                            <FormattedMessage id={key} />
                          </th>
                          <td>{value}</td>
                        </tr>
                      )
                  )}
                </table>
                <div
                  className={styles.removeBtn}
                  onClick={() => remove(index)}
                ></div>
              </div>
            </div>
          ))}
        </Col>
      </Row>
    </form>
  );
};
export default EducationForm;
