import React from "react";
import { Col, Row } from "react-bootstrap";
import { useFieldArray, useForm, useFormContext } from "react-hook-form";
import { FormattedMessage } from "react-intl";
import { v4 } from "uuid";
import styles from "./index.module.scss";

const JobsForm = () => {
  const { control } = useFormContext();
  const { fields, append, remove } = useFieldArray({
    name: "jobs",
    control: control,
  });
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  //   handlers Mehtods
  const addJobHandler = (data) => {
    append(data);
  };
  return (
    <form onSubmit={handleSubmit(addJobHandler)}>
      <Row className={styles.innrRow}>
        <Col xs={12}>
          <input
            type="text"
            placeholder="المكان"
            className={`${errors?.["place"] ? styles.errorField : ""}
            `}
            {...register("place", { required: true })}
          />
        </Col>
        <Col xs={12} lg={6}>
          <input
            type="text"
            placeholder="المدينة"
            className={`${errors?.["city"] ? styles.errorField : ""}
            `}
            {...register("city", { required: true })}
          />
        </Col>
        <Col xs={12} lg={6}>
          <input
            type="text"
            placeholder="البلد"
            className={`${errors?.["country"] ? styles.errorField : ""}
            `}
            {...register("country", { required: true })}
          />
        </Col>
        <Col xs={12}>
          <input
            type="text"
            placeholder="الوظيفة"
            className={`${errors?.["title"] ? styles.errorField : ""}
            `}
            {...register("title", { required: true })}
          />
        </Col>
        <Col xs={12} lg={6}>
          <input
            type="date"
            className={`${errors?.["startDate"] ? styles.errorField : ""}
            `}
            placeholder="تاريخ بداية الوظيفة"
            {...register("startDate", { required: true })}
          />
        </Col>
        <Col xs={12} lg={6}>
          <input
            type="date"
            className={`${errors?.["endDate"] ? styles.errorField : ""}
            `}
            placeholder="تاريخ انتهاء الوظيفة"
            {...register("endDate", { required: true })}
          />
        </Col>
        <Col xs={12}>
          <textarea
            name=""
            id=""
            cols="30"
            rows="5"
            className={`${errors?.["reason"] ? styles.errorField : ""}
            `}
            placeholder="سبب ترك الوظيفة (اختيار)"
            {...register("reason", { required: true })}
          />
        </Col>
        <Col xs={12}>
          <input
            type="submit"
            value=" إضافة وظيفة +"
            className={styles.addField}
          />
        </Col>
        <Col xs={12}>
          {fields?.map((tableItem, index) => (
            <div className={styles["table"]}>
              <div className={styles["table-body"]}>
                <table>
                  {Object.entries(tableItem).map(
                    ([key, value]) =>
                      key !== "id" && (
                        <tr key={v4()}>
                          <th>
                            <FormattedMessage id={key} />
                          </th>
                          <td>{value}</td>
                        </tr>
                      )
                  )}
                </table>
                <div
                  className={styles.removeBtn}
                  onClick={() => remove(index)}
                ></div>
              </div>
            </div>
          ))}
        </Col>
      </Row>
    </form>
  );
};

export default JobsForm;
