import React, { useEffect } from "react";
import { Col, Row } from "react-bootstrap";
import { useFormContext } from "react-hook-form";
import { FormattedMessage } from "react-intl";
import { useSelector } from "react-redux";
import styles from "./index.module.scss";

const InfoForm = () => {
  const {
    meta: { locale },
    empolyee: { specialties, subscriptionTypes, nationalties },
  } = useSelector((state) => state);
  const {
    register,
    formState: { errors },
  } = useFormContext();
  useEffect(() => {
    // registe the hidden inputs
  }, []);

  // TODO Compelte the needed methods for the form

  return (
    <Row className={styles.innrRow}>
      <Col xs={12} lg={4} className={styles.pictureInput}>
        <label
          htmlFor="picture"
          className={`${errors?.["picture"] ? styles.errorField : ""}
        `}
        ></label>
        <input
          type="file"
          id="picture"
          {...register("picture", { required: true })}
        />
      </Col>
      <Col xs={12} lg={8}>
        <Row className={styles.innrRow}>
          <Col xs={12} lg={6}>
            <input
              type="text"
              className={`${errors?.["fristName"] ? styles.errorField : ""}
              `}
              placeholder="الاسم الاول"
              {...register("fristName", { required: true })}
            />
          </Col>
          <Col xs={12} lg={6}>
            <input
              type="text"
              placeholder="اسم العائلة"
              className={`${errors?.["lastName"] ? styles.errorField : ""}
              `}
              {...register("lastName", { required: true })}
            />
          </Col>
          <Col xs={12}>
            <input
              type="text"
              placeholder="البريد الالكتروني"
              className={`${errors?.["email"] ? styles.errorField : ""}
              `}
              {...register("email", { required: true })}
            />
          </Col>
        </Row>
      </Col>
      <Col xs={12}>
        <input
          type="text"
          placeholder="رقم الهاتف"
          className={`${errors?.["phone"] ? styles.errorField : ""}
              `}
          {...register("phone", { required: true })}
        />
      </Col>
      <Col xs={12}>
        <input
          type="text"
          placeholder="رقم الهاتف اخر (اختيار)"
          {...register("phone_optinal")}
        />
      </Col>
      <Col xs={12}>
        <select
          className={`${errors?.["specialty_id"] ? styles.errorField : ""}
        `}
          {...register("specialty_id", { required: true })}
        >
          {specialties.map((specialty) => (
            <option value={specialty.id}>
              {locale === "ar" ? specialty.name_ar : specialty.name_en}
            </option>
          ))}
        </select>
      </Col>
      <Col xs={12} lg={6}>
        <input
          type="text"
          className={`${errors?.["city"] ? styles.errorField : ""}
        `}
          placeholder="المدينة"
          {...register("city", { required: true })}
        />
      </Col>
      <Col xs={12} lg={6}>
        <input
          type="text"
          className={`${errors?.["zip_code"] ? styles.errorField : ""}
          `}
          placeholder="الرمز البريدي"
          {...register("zip_code", { required: true })}
        />
      </Col>
      <Col xs={12}>
        <input
          type="date"
          className={`${errors?.["date_of_birth"] ? styles.errorField : ""}
          `}
          placeholder="تاريخ الميلاد"
          {...register("date_of_birth", { required: true })}
        />
      </Col>
      <Col xs={12}>
        <input
          type="text"
          className={`${errors?.["place_of_birth"] ? styles.errorField : ""}
          `}
          placeholder="مكان الميلاد"
          {...register("place_of_birth", { required: true })}
        />
      </Col>
      <Col xs={12}>
        <input
          type="text"
          className={`${errors?.["current_place"] ? styles.errorField : ""}
          `}
          placeholder="مكان الاقامة الحالي"
          {...register("current_place", { required: true })}
        />
      </Col>
      <Col xs={12}>
        <select
          className={`${errors?.["sex"] ? styles.errorField : ""}
          `}
          {...register("sex", { required: true })}
        >
          <option value={null} disabled>
            الجنس
          </option>
          <option value={"mele"}>ذكر</option>
          <option value={"femele"}>انثي</option>
        </select>
      </Col>
      <Col xs={12}>
        <select
          className={`${errors?.["country_id"] ? styles.errorField : ""}
          `}
          {...register("country_id", { required: true })}
        >
          <option value={null} disabled>
            <FormattedMessage id="selectCountry" />
          </option>
          {nationalties.map((nationalty) => (
            <option value={nationalty.id}>{nationalty?.[locale]}</option>
          ))}
        </select>
      </Col>
      <Col xs={12}>
        <select
          className={`${errors?.["type_of_id"] ? styles.errorField : ""}
         `}
          {...register("type_of_id", { required: true })}
        >
          <option value={null} disabled>
            نوع اثبات الشخصية
          </option>
          <option value={"passport"}>جواز سفر</option>
          <option value={"id"}>بطاقة هوية</option>
        </select>
      </Col>
      <Col xs={12}>
        <input
          type="text"
          className={`${errors?.["id_number"] ? styles.errorField : ""}
         `}
          placeholder="رقم اثبات الشخصية"
          {...register("id_number", { required: true })}
        />
      </Col>
      <Col xs={12}>
        <select
          className={`${errors?.["social_status"] ? styles.errorField : ""}
         `}
          {...register("social_status", { required: true })}
        >
          <option value={null} disabled>
            الحالة الاجتماعية
          </option>
          <option value={"marriage"}>متزوج / متزوجة</option>
          <option value={"divorced"}>مطلقة</option>
          <option value={"single"}>اعزب</option>
        </select>
      </Col>
    </Row>
  );
};

export default InfoForm;
