import React, { useEffect } from "react";
import { Accordion, Col, Container, Row } from "react-bootstrap";
import { FormProvider, useForm } from "react-hook-form";
// needed styles
import styles from "./index.module.scss";
import InfoForm from "./InfoForm";
import EducationForm from "./EducationForm";
import SkillsForm from "./SkillsForm";
import JobsForm from "./JobsForm";
import { addCv } from "api/employee";
import { useNavigate } from "react-router-dom";
import { FormattedMessage } from "react-intl";

const GetEmpolyeePage = () => {
  const methods = useForm();
  const navigate = useNavigate();
  const { handleSubmit, register, getValues } = methods;
  const submitHandler = async ({ fristName, lastName, picture, ...data }) => {
    try {
      data = await addCv({
        ...data,
        name: `${fristName} ${lastName}`,
        picture: picture[0]?.name,
      });
      navigate(`/profile/CV/${data.id}`);
    } catch (error) {
      alert(error.message);
    }

    // TODO: add the rest of the data and file upload before submit
  };

  useEffect(() => {}, []);

  return (
    <div className={styles.body}>
      <Container>
        <Row>
          {/* TODO Complete the two other forms  */}
          <Col xs={12} className={styles.headers}>
            <h3>التوظيف بالخارج</h3>
            <p>نقدم خدمات التوظيف بالخارج لكل من الشركات والأفراد</p>
          </Col>
          <FormProvider {...methods}>
            <Col xs={12} className={styles.formContainer}>
              <Accordion defaultActiveKey={"0"}>
                <Row className={styles.form}>
                  <Col xs={12}>
                    <Accordion.Item eventKey="0" className={styles.formItem}>
                      <Accordion.Header>
                        <FormattedMessage id="personalInfo" />
                      </Accordion.Header>
                      <Accordion.Body>
                        <InfoForm />
                      </Accordion.Body>
                    </Accordion.Item>
                  </Col>
                  <Col xs={12}>
                    <Accordion.Item eventKey="1" className={styles.formItem}>
                      <Accordion.Header>
                        <FormattedMessage id="education" />
                      </Accordion.Header>
                      <Accordion.Body>
                        <EducationForm />
                      </Accordion.Body>
                    </Accordion.Item>
                  </Col>
                  <Col xs={12}>
                    <Accordion.Item eventKey="2" className={styles.formItem}>
                      <Accordion.Header>
                        <FormattedMessage id="jobs" />
                      </Accordion.Header>
                      <Accordion.Body>
                        <JobsForm />
                      </Accordion.Body>
                    </Accordion.Item>
                  </Col>
                  <Col xs={12}>
                    <Accordion.Item eventKey="3" className={styles.formItem}>
                      <Accordion.Header>
                        <FormattedMessage id="experience" />
                      </Accordion.Header>
                      <Accordion.Body>
                        <SkillsForm />
                      </Accordion.Body>
                    </Accordion.Item>
                  </Col>
                  <form onSubmit={handleSubmit(submitHandler)}>
                    <Col xs={12}>
                      <label htmlFor="policy" className={styles.policy}>
                        <input
                          id="policy"
                          type="checkbox"
                          {...register("policy", { required: true })}
                        />
                        <span>اوافق علي الشروط والاحكام</span>
                      </label>
                      <button type="submit">ارسال البيانات</button>
                    </Col>
                  </form>
                </Row>
              </Accordion>
            </Col>
          </FormProvider>
        </Row>
      </Container>
    </div>
  );
};

export default GetEmpolyeePage;
