import React from "react";
import styles from "./index.module.scss";
import { useFieldArray, useForm, useFormContext } from "react-hook-form";
import { Col, Row } from "react-bootstrap";
import { FormattedMessage, useIntl } from "react-intl";
import { v4 } from "uuid";
import { uploadApi } from "api/upload";
import { toast } from "react-toastify";

const SkillsForm = () => {
  const { formatMessage } = useIntl();
  const { control } = useFormContext();
  const { fields, append, remove } = useFieldArray({
    name: "experiences",
    control: control,
  });
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  //   handlers Mehtods
  const addSkillHandler = async ({ certification, ...data }) => {
    const file = new FormData();
    file.append("files", certification[0]);
    try {
      const fileResponse = await uploadApi(file);
      append({ ...data, certification: fileResponse?.[0] });
      reset();
    } catch (error) {
      toast.error(error);
    }
  };

  return (
    <form onSubmit={handleSubmit(addSkillHandler)}>
      <Row className={styles.innrRow}>
        <Col xs={12}>
          <input
            className={errors?.["skill"] ? styles.errorField : ""}
            type="text"
            placeholder="المهارة"
            {...register("skill", { required: true })}
          />
        </Col>
        {/* <Col xs={12}>
          <h6> المستوي</h6>
        </Col>
        <Col xs={12}>
          <input
            className={styles.rangeInput}
            type="range"
            {...register("skillCount")}
            max={100}
            min={1}
          />
        </Col> */}
        <Col xs={12}>
          <label
            htmlFor="skillCertification"
            className={`${styles.fileInput} ${
              errors?.["certification"] ? styles.errorField : ""
            }
            `}
          >
            <p>
              <FormattedMessage id="certification" />
            </p>
            <input
              id="skillCertification"
              name="certification"
              type="file"
              {...register("certification", {
                required: true,
                type: "mix",
              })}
            />
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M14 0a5 5 0 0 1 5 5v12a7 7 0 1 1-14 0V9h2v8a5 5 0 0 0 10 0V5a3 3 0 1 0-6 0v12a1 1 0 1 0 2 0V6h2v11a3 3 0 1 1-6 0V5a5 5 0 0 1 5-5Z"
                fill="currentColor"
              />
            </svg>
          </label>
        </Col>
        <Col xs={12}>
          <textarea
            name=""
            id=""
            cols="30"
            rows="5"
            placeholder={formatMessage({ id: "notesPlaceholder" })}
            className={errors?.["notes"] ? styles.errorField : ""}
            {...register("notes", { required: true })}
          />
        </Col>
        <Col xs={12}>
          <input
            type="submit"
            value=" إضافة مهارة +"
            className={styles.addField}
          />
        </Col>
        <Col xs={12}>
          {fields?.map((tableItem, index) => (
            <div className={styles["table"]}>
              <div className={styles["table-body"]}>
                <table>
                  {Object.entries(tableItem).map(
                    ([key, value]) =>
                      key !== "id" && (
                        <tr key={v4()}>
                          <th>
                            <FormattedMessage id={key} />
                          </th>
                          <td>{value}</td>
                        </tr>
                      )
                  )}
                </table>
                <div
                  className={styles.removeBtn}
                  onClick={() => remove(index)}
                ></div>
              </div>
            </div>
          ))}
        </Col>
      </Row>
    </form>
  );
};

export default SkillsForm;
